
.network-popup-total-container{
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: linear-gradient(to right,aqua,violet);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 9999;
}
.network-popup-inner-container{
    background-color: transparent;
    border: 1px solid greenyellow;
    padding: 20px;
    width:210px;
   display: flex;
   flex-direction: column;
   align-items: center;
   
}
.network-popup-set-img{
    height:60px;
    width:60px;
}
.network-popup-para{
    margin-top: 20px;
    color:white;
    font-weight: 600;
    text-align: center;
}

